.reg{
    background: linear-gradient(270deg, #ececec, whitesmoke);
    background-size: 400% 400%;
    -webkit-animation: sign 30s ease infinite;
    -moz-animation: sign 30s ease infinite;
    animation: sign 30s ease infinite;
    height: 100vh;
    padding-top: 180px;
}
.darkLog{
    background: #121212;
}
@-webkit-keyframes sign {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes sign {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes sign {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
.signBox{
    font-family: "lato" , sans-serif;
    color: #fff;
    width: 70%;
    background: linear-gradient(45deg , #32407b  , #162447);
    padding: 25px 25px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 5px rgba(78, 78, 78, 0.363);
}
.darkBox{
    background: #404040 !important;
    color: #ffff;
}
.darkBt{
    background: #121212 !important;
}
.loginBox{
    background: linear-gradient(45deg , #192a56  , #353b48);
}
.input{
    background: none !important;
    outline: none !important;
    transition: all 0.3s;
}
.input::placeholder{
    color: white !important;
}
.label{
    font-family: 'Open Sans', sans-serif;
    font-weight: bolder;
}
.input::va{
    color: white !important;
}
.input:focus{
    outline: none !important;
    background: white !important;
    color: #000;
}
.darkInp{
    background: #ececec !important;
}
.darkInp::placeholder{
    color: #121212 !important;
}

@media screen and (min-width:300px) and (max-width:760px){
    .reg{
        padding-top: 125px;
        height: auto;
        padding-bottom: 340px;
    }
    .login{
        padding-top: 180px;
    }
    .signBox{
        width: 95%;
    }
}