.pro{
    margin-top: 2%;
  
}

.userPro{
    height: 250vh;
     background: #ecf0f1;
     padding-bottom: 50px;
}
.userPro .editOp{
    filter: blur(5px) !important;
}
.proPic{
    width: 90px;
    height: 90px;
    margin: 2% auto;
}
.proPic img{
    width: 100%;
    height: 100%;
}
.picture{
    padding: 20px 10px;
    background: navy;
    width: 50%;
    margin: 2% auto;
    text-align: center;
    border-radius: 10px;
}
.picture label{
    display: block;
}
.picture input{
    margin-left: -5%;
    margin-top: 1%;
  
}
.picture .sub .btn{
    width: 100%;
}
.jumb{
    margin-top: 1%;
    background: none !important;
}
.jumb h2{
    font-family: 'Fira Sans', sans-serif;
    
}
.jumb h3{
    font-family: "Lato", sans-serif;
    margin-top: 2%;
}
.userflex{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    flex-shrink: 100;
    height: 200px;
    margin: 5px;
    margin-top: -5%;
  
}
.userBx{
    flex: 0 1 30%;
    
     margin: 5px;
    height: 250px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    text-align: center;
 padding: 5px 5px;
    border-radius: 5px;
background-color: gray;
background-blend-mode: multiply;
}
.userBx h5 {
    width: 100%;
    margin-top: 60%;
    font-family: 'Open Sans', sans-serif;
    font-weight: bolder;
    color: white;
    text-shadow: 2px 2px black;
    background: #bdc3c791;
    padding: 5px 0;
    
  
}
.userImg{
    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-bottom: 2%;
   
}
.userImg img{
    width: 100%;
    height: 100%;
    border: 3px solid #d2d2d3;
    border-radius: 100% !important;
   
}
.editCard{
    position: fixed;
    width: 60%;
    top: 30%;
    left: 20%;
    font-family: 'Open Sans', sans-serif;
    box-shadow: 0px 0px 5px 2px grey;
    background: rgb(247, 247, 247);
}
.editForm{
    padding: 10px 10px;
}
.closeEdt{
    position: absolute;
    left: 97%;
    top: -12%;
    padding: 5px 5px;
    font-size: 35px;
    cursor: pointer;
    color: black;
 
    
}
.darkRow{
    background: #121212 !important;
    color: #ffff !important;
}
.editDark{
    background: #181818 !important;
    box-shadow: none !important;

}
.closeDark{
    color: #ffff;
}
.proDark{
    background: #ecf0f1 !important;
}
@media screen and (min-width:300px) and (max-width:760px){
    .profileBox:first-child{
        text-align: center;
        padding-top: 30%;
    }
    .profileBox:nth-child(2){
        height: 100vh !important;
    }
    .rowPro{
        display: flex;
        flex-flow: column;
    }
   .userflex{
    display: flex;
    flex-flow: row wrap;
       height: auto;
       padding-bottom: 300px;
       margin-top: -15%;
   }
   .jumb{
       margin-top: 12%;
   }
   .userBx{
       width: 90%;
       flex: 0 1 48%;
        height: 200px;
       margin: 5px;
      margin: 7% auto;
   }
   .userBx h5{
       margin-top: 100%;
       font-size: 18px;
   }
   .set{
       margin-right: 3%;
       margin-top: 4%;
   }
   .userPro{
       height: auto !important;
   }
   .editCard{
       width: 90%;
       left: 5%;
     
   }
   .closeEdt{
       left: 92%;
   }
  
}