
.bar{
    height: 60px;
    background: #1266F1;
  padding: 8px 0;
  box-shadow: 0px 0px 10px 2px black !important;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

}

.nav-menu {
    background-color: #18191a;
    width: 220px;
    height: 100vh;
    display: flex;
    justify-content: center;
    z-index: 1000;
    position: fixed;
    font-family: 'Lato', sans-serif;
    font-weight: bolder;
    top: 0;
     right: -100%;
    transition: 850ms;
    box-shadow: 0px 0px 8px 5px black;
    cursor: pointer;
  }
  
  .nav-menu.active {
    right: 0;
    transition: 350ms;
  
  }
  /* font-family: 'Lato', sans-serif;
  font-family: 'Open Sans', sans-serif; */
 
  .nav-menu-items{
    list-style: none;


  }
  .navbar-toggle{
    margin-bottom: 10%;
  }
  .menu-bars{
    color: white;
    margin-bottom: 20%;
    margin-top: 10%;
    cursor: pointer !important;
  }
.menu-Links{
  color: white;
  margin-top: 70%;
 text-align: center;

 

  
}
.links{
  color: white;
  padding: 10px 25px;
}
.links:hover{
  text-decoration: none;
  color: white;
  background: #F93154;
  border-radius: 20px;
  
}
.row{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    justify-items:center;

}
/* .menuBox{
    width: 100%;
} */
.ham{
    margin-left: 20%;
    margin-top: 0.5%;
    cursor: pointer;
}
.input::placeholder{
  color: #FBFBFB;
  font-family: 'Open Sans', sans-serif;

}
.input:focus{
  color: #FBFBFB;
  font-family: 'Open Sans', sans-serif;
 
}
.input:active{
  outline: none !important;
}
.img{
  width: 50px;
  height: 50px;
  margin: 0 auto;
}
.img img{
  width: 100%;
  height: 100%;
}
.proLi{
  width: 100% !important;
  padding:10px 30px !important;
  background: #1266F1;
  border-radius: 20px;
  color: #fff;
}
.logLi{
  width: 100% !important;
  padding:10px 30px !important;
  background: #c23616;
  border-radius: 20px;
  color: #fff;
}
.drkB{
  background: #18191a !important;
}



@media screen and (min-width:300px) and (max-width:760px){
  .searchBox{
    display: none;
  }
  .nav-menu{
    width: 200px;
    text-align: center !important;
  }
  .nav-menu-items{
    margin-right: 25%;
    width: 100%;
  }
  .menu-Links{
    font-size: 15px;
    width: 100%;
  }
  .links{
    width: 100%;
  }
  .ham{
    margin-right: 4%;
    margin-top: 2%;
  }
  .img{
    margin-left: 3%;
  }
}