.blur{
    transition: all 0.2s;
    filter: blur(5px) !important ;
}
.add-btn{
    width: 50px;
    height: 50px;
    z-index: 999;
    background: purple !important;
    border-radius: 100% !important;
    position: fixed;
    top: 80%;
    left: 10%;
    transition: all 0.3s;
    /* box-shadow: 0px 0px 10px 1px black; */
    animation: add infinite 0.5s alternate;
} 
.open{
    left: 73%;
    top: 18%;
    animation: none;
    transform: rotate(45deg);
    transition: all 0.3s;
    box-shadow: 0px 0px 10px 1px black;
    /* display: none; */
}
@keyframes add {
    from{
        box-shadow: 0;
        transform: rotate(0);
    }
    to{
        box-shadow: 0px 0px 10px 1px black;
        transform: rotate(90deg);
    }
}
.btn{
    outline: none !important;
}
