.toggle-container{
    width:90%;
    position: relative;
    margin-top: 5%;
  }
  #checkbox{
    position: relative;
    width:50px;
    height: 15px;
    background: black;
    appearance: none !important; 
    border-radius: 20px;
    outline: none;
  transition: 0.3s linear;
  }
  #checkbox:checked{
    background: #dfdfdf;
  }
  #checkbox::before{
    content: '';
    position: absolute;
    width: 40%;
    height: 20px;
    border-radius: 100px;
    top: -20%;
    left: 0;
    transition: 0.3s linear;
    transform: scale(1.1);
    background: rgb(179, 178, 178);
    box-shadow: 0 px 2px 5px rgba(255, 255, 255, 0.486);
  }
  #checkbox:checked::before{
    left: 60%;
  }
